<template>
    <section>
        <section class="edit-menu flex">
            <v-btn fab dark color="green" title="Save" @click="saveUserPage">
                <v-icon dark>save</v-icon>
            </v-btn>

            <v-btn fab dark color="green" title="Publish" @click="changeMode('publish')" v-if="modePage === 'edit'">
                <v-icon dark>publish</v-icon>
            </v-btn> 

             <v-btn fab dark color="green" title="Edit" @click="changeMode('edit')" v-if="modePage === 'publish'">
                <v-icon dark>edit</v-icon>
            </v-btn> 

            <!-- <v-btn fab dark color="blue" class="open-toolbar" title="Add area">
                <v-icon dark>add</v-icon>
            </v-btn> -->
        </section>
    </section>
</template>

<script>
import { ACT_UPDATE_USER } from "@/store/userModule.js";

import {
  eventBus,
  EVENT_UPDATE_USER,
  EVENT_CHANGE_MODE_PAGE,
  EVENT_OPEN_TOOL_BAR,
  EVENT_TOGGLE_HEADER_PAGE
} from "@/services/event-bus-service.js";

export default {
  name: "edit-menu-cmp",
  props: {
    modePage: String
  },
  components: {},
  data() {
    return {};
  },
  methods: {
    saveUserPage() {
      eventBus.$emit(EVENT_UPDATE_USER);
    },
    changeMode(mode) {
      eventBus.$emit(EVENT_CHANGE_MODE_PAGE, mode);
      eventBus.$emit(EVENT_SHOW_HEADER_PAGE); 
      if (mode === "publish") {
        eventBus.$emit(EVENT_OPEN_TOOL_BAR, null); // In publish mode -> close tool bar
        
      }
    }
  },
  created() {}
};
</script>

<style scoped lang="scss">
.edit-menu {
  position: fixed;
  display: flex;
  left: 5%;
  bottom: 5%;
  z-index: 100000;
}

@media only screen and (max-width: 600px) {
    .edit-menu {
        position: fixed;
        flex-direction: row;
        height: 80px;
        top: 0;
        left: 0;
    }
}
</style>
